@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
html,body{margin:0;padding:0}.loginPage-wrapper{display:flex;width:100vw;height:100vh;justify-content:center;align-items:center;align-content:center;background-image:url("https://i.ibb.co/BqYPzz9/stomp.jpg");background-size:cover;background-position:center center;font-family:'Open Sans';opacity:0.9}.loginPage-wrapper .login{display:flex;flex-direction:column;justify-content:space-between;background:#42105244;padding:25px;border-radius:50px;box-shadow:15px 3px 79px -25px rgba(255,255,255,0.85)}.loginPage-wrapper .login .login-inputs{color:#e1e9ee;display:flex;height:300px;flex-direction:column;justify-content:space-between;align-content:space-between}.loginPage-wrapper .login .login-inputs .login-title{color:#e1e9ee;font-size:30px;text-align:center;font-weight:200}.loginPage-wrapper .login .login-inputs .login-input,.loginPage-wrapper .login .login-inputs .login-submit{color:#e1e9ee;border-radius:20px;font-size:25px;text-indent:5px;padding-top:10px;padding-bottom:10px;outline:none;background:rgba(4,0,61,0.39);border:2px #034a85 solid}.loginPage-wrapper .login .login-inputs .login-input::-webkit-input-placeholder, .loginPage-wrapper .login .login-inputs .login-submit::-webkit-input-placeholder{color:#a8a8a8}.loginPage-wrapper .login .login-inputs .login-input:-ms-input-placeholder, .loginPage-wrapper .login .login-inputs .login-submit:-ms-input-placeholder{color:#a8a8a8}.loginPage-wrapper .login .login-inputs .login-input::-ms-input-placeholder, .loginPage-wrapper .login .login-inputs .login-submit::-ms-input-placeholder{color:#a8a8a8}.loginPage-wrapper .login .login-inputs .login-input::placeholder,.loginPage-wrapper .login .login-inputs .login-submit::placeholder{color:#a8a8a8}.loginPage-wrapper .login .login-inputs .login-submit{text-transform:uppercase;text-align:center;cursor:pointer}

