.loginPage-wrapper {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  background-image: url('https://i.ibb.co/BqYPzz9/stomp.jpg');
  background-size: cover;
  background-position: center center;
  font-family: 'Open Sans';
  opacity: 0.9;
  .login {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #42105244;
    padding: 25px;
    border-radius: 50px;
    -webkit-box-shadow: 15px 3px 79px -25px rgba(243, 240, 240, 0.85);
    -moz-box-shadow: 15px 3px 79px -25px rgba(255, 255, 255, 0.85);
    box-shadow: 15px 3px 79px -25px rgba(255, 255, 255, 0.85);
    .login-inputs {
      color: #e1e9ee;
      display: flex;
      height: 300px;
      flex-direction: column;
      justify-content: space-between;
      align-content: space-between;

      .login-title {
        color: #e1e9ee;
        font-size: 30px;
        text-align: center;
        font-weight: 200;
      }
      .login-input,
      .login-submit {
        color: #e1e9ee;
        border-radius: 20px;
        font-size: 25px;
        text-indent: 5px;
        padding-top: 10px;
        padding-bottom: 10px;
        outline: none;
        background: rgba(4, 0, 61, 0.39);
        border: 2px rgb(3, 74, 133) solid;

        &::placeholder {
          color: #a8a8a8;
        }
      }
      .login-submit {
        text-transform: uppercase;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}
